<template>
  <div id="app">
      <h1>小葵's Blog</h1>
        ———— From Internet,For Internet.
      <br>
      <br>
      <div style="float:left;margin:30px;text-align:left" >
      <h3>List:</h3>
        <p v-for="id in list"  v-bind:key="id">
          <a @click="view(id)"> <h3>{{ id }}</h3></a>
        </p>
      </div>

      <div style="margin:30px;margin-left:20%;text-align:left">
        <markdown-it-vue class="md-body" :content="content" :options="options" />
      </div>
  </div>
</template>

<script>

import axios from 'axios'
import MarkdownItVue from 'markdown-it-vue'
import 'markdown-it-vue/dist/markdown-it-vue.css'

export default {
  name: 'App',
  components: {
    MarkdownItVue
  },
  data () {
    return {
      list : [],
      content: '# 我是小葵，欢迎访问我的Blog。',
      options: {
        markdownIt: {
          linkify: true
        }
      }
    }
  },
  
  mounted () {
    axios
      .get('https://home.xiaokv.com:3000/list')
      .then(response => (this.list = response.data))
      .catch(function (error) { // 请求失败处理
        console.log(error);
      });
  },
  methods: {
    view: function (id){
      axios
      .get('https://home.xiaokv.com:3000/read/' + id)
      .then(response => (this.content = response.data))
      .catch(function (error) { // 请求失败处理
        console.log(error);
      });
    }
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>



